import React from "react";
import axios from "axios";
import {baseUrl} from "../../../config";
import {Avatar, Button, Icon, Modal, Spin} from "antd";
import wx from "weixin-js-sdk";
import './index.less'

export default class ContentSkill extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
        this.fetchJineng();
        // this.share();

    };
    componentDidMount() {

    }

    fetchJineng = () => {
        axios.get(
            baseUrl + '/api/get_jineng',
            {
                params: {
                    'id_jineng': this.props.match.params.id,
                    'token': localStorage.customer_token,//*
                }
            })
            .then((r) => {
                console.log(r.data);
                this.setState({
                    dataSource: r.data,
                });
                this.share();
                console.log(this.state.dataSource.jineng[0])
            })
    };
    share = () => {
        const d = this.state.dataSource;
        console.log(d,this.state);
        const convertTpl = (id) => {
            const d = {
                1: '快递代取',
                2: '电费代充',
                3: '电影票代买',
                4: '跑腿',
                5: '账号出租',
                6: '课程服务',
                7: '拍照',
                8: '修图',
                9: '化妆',
                10: '游戏陪玩',
                11: '电车出租',
                12: '司机',
                13: '服务员',
                14: '礼仪',
                15: '模特',
                16: '配音',
                17: '打印',
            };
            return d[id]
        };
        axios.get(baseUrl + '/api/get_jssdk',
            {
                params: {
                    url: window.location.href.split('#')[0]
                }
            }).then(res => {
            const params = {
                debug: false,
                appId: res.data.appId,
                timestamp: res.data.timestamp,
                nonceStr: res.data.nonceStr,
                signature: res.data.signature,
                jsApiList: [
                    'onMenuShareTimeline',
                    'onMenuShareAppMessage'
                ]
            };
            wx.config(params);
            wx.ready(function () {
                // config信息验证成功后会执行ready方法
                //通过ready接口处理成功验证
                wx.onMenuShareAppMessage({
                    // 分享给朋友
                    title: `【小蓝兼职】${d.jineng[0].bei_zhu}`, // 分享标题
                    desc: `${convertTpl(d.jineng[0].jineng_tpl)}达人`,
                    link: res.data.str, // 分享链接
                    imgUrl:
                        `${baseUrl}/storage/9XH9nlr8bEmonCViyLAV0qBf3HHtogDNKSZqMyT9.png`, // 分享图标
                    type: "", // 分享类型,music、video或link，不填默认为link
                    dataUrl: "", // 如果type是music或video，则要提供数据链接，默认为空
                    success: function (res) {
                        // 用户确认分享后执行的回调函数
                    },
                    cancel: function () {
                        // 用户取消分享后执行的回调函数
                    }
                });
                wx.onMenuShareTimeline({
                    //分享朋友圈
                    title: `【小蓝兼职】${d.jineng[0].bei_zhu}`, // 分享标题
                    desc: `${convertTpl(d.jineng[0].jineng_tpl)}达人`,
                    link: res.data.str, // 分享链接
                    imgUrl:
                        `${baseUrl}/storage/9XH9nlr8bEmonCViyLAV0qBf3HHtogDNKSZqMyT9.png`, // 分享图标
                    success: function (res) {
                        // 用户确认分享后执行的回调函数
                    },
                    cancel: function () {
                        // 用户取消分享后执行的回调函数
                    }
                });
            });
        });
    };
    convertTpl = (id) => {
        const d = {
            1: '快递代取',
            2: '电费代充',
            3: '电影票代买',
            4: '跑腿',
            5: '账号出租',
            6: '课程服务',
            7: '拍照',
            8: '修图',
            9: '化妆',
            10: '游戏陪玩',
            11: '电车出租',
            12: '司机',
            13: '服务员',
            14: '礼仪',
            15: '模特',
            16: '配音',
            17: '打印',

        };
        return d[id]
    };
    goBack = () => {
        document.location.hash = '#/skill'

    };
    // 联系方式弹窗
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    showCommunications = (id) => {
        console.log(id);

        axios.get(
            baseUrl + '/api/curd_communication',
            {
                params: {
                    token: localStorage.customer_token,
                    method: 'solo_get',
                    id_communication: id,
                }
            })
            .then((r) => {
                this.setState({
                    visibleCommunications: true,
                    modalContent: <div>
                        <div>
                            <h2 className='name'>手机号:{r.data.communications[0].phone}</h2>
                            <img src={`${baseUrl}/${r.data.communications[0].wx_png}`} alt=''
                                 className='avatar'
                                 width='100%'
                            />
                            <h5>提示：长按识别二维码添加微信</h5>
                        </div>
                    </div>,
                });
            });
    };

    render() {
        const d = this.state.dataSource;
        return (
            <div className='content-skill'>
                <div className='back-to-last'>
                    <span onClick={this.goBack}>
                        <Icon type="left"/>
                        <span>返回</span>
                    </span>
                </div>
                {
                    d !== undefined ?
                        <div>
                            <div className='poster-info'>
                                <div className='avatar-name'>
                                    <Avatar size={40} shape='square'
                                            src={`${baseUrl}/${d.senter_info[0].avatarUrl}`}
                                            className='avatar'>头像</Avatar>
                                    <span className='name'>{d.senter_info[0].nickName}</span>
                                </div>
                            </div>
                            <div className='skill-main'>
                                <div className='skill-info'>
                                    {d.jineng[0].bei_zhu}
                                </div>
                                <div className='skill-img'>
                                    {d.jineng_files.length!==0?
                                    <img
                                        className='skill-img'
                                        src={`${baseUrl}/${d.jineng_files[0].content}`}
                                        alt=''
                                        onClick={() => {
                                            const WeixinJSBridge = window.WeixinJSBridge;
                                            WeixinJSBridge.invoke("imagePreview", {
                                                "urls": [`${baseUrl}/${d.jineng_files[0].content}`],
                                                "current": `${baseUrl}/${d.jineng_files[0].content}`
                                            });
                                        }}
                                    />
                                    :null
                                    }
                                </div>
                                <div className='find-him'>
                                    <Button
                                        onClick={this.showCommunications.bind(this, d.jineng[0].id_communication)}
                                    >
                                        找他
                                    </Button>
                                </div>
                            </div>
                        </div>
                        : <Spin/>

                }
                <Modal
                    visible={this.state.visibleCommunications}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    style={{textAlign: 'center'}}
                >
                    {this.state.modalContent}
                </Modal>
            </div>
        )
    }
}